<template lang="pug">

    v-card 
        v-card-title 
            h2 数据表

        v-card-text(class="pa-5")
            v-data-table(
                :headers="headers"
                :items="tabledata"
                :options.sync="options"
                no-data-text="暂无数据"
                dense
                :server-items-length="total_rows"
                :footer-props="{'items-per-page-options': items_per_page_options,'show-first-last-page':true}"
            )
                template(v-slot:top)
                    v-toolbar(flat class="mb-8")
                        v-row
                            v-col(cols="12" md="6" lg="9")
                                v-btn(class="mb-2 text-capitalize" color="primary" @click="update_col_page_data" )
                                    v-icon(class="mr-2" ) mdi-reload
                                    span 刷新
                        
                            v-col(cols="12" md="6" lg="3")
                                v-btn(class="mb-2 text-capitalize" color="primary" @click="download_page_data" )
                                    v-icon(class="mr-2") mdi-cloud-download
                                    span 下载数据表

</template>

<script>
import { sync,get} from 'vuex-pathify'
export default {
    data:() => ({
        table_data:[
            {field:"井号",filter_type:"等于",filter_value:"A10H"},
            {field:"区块",filter_type:"等于",filter_value:"南泥湾"},
            ],
        header:[{text:"选择字段",value:"field"},{text:"筛选类型",value:"filter_type"},{text:"值",value:"filter_value"}],
        options:{},
    }),
    computed: {
        items_per_page_options:sync('db/items_per_page_options'),
        // 显示的数据库配置
        collection_config:sync('db/collection_config'),
        // 表头
        headers:get('db/headers'),
        // 表内数据
        tabledata:get('db/tabledata'),
        // 总行数
        total_rows:get('db/total_rows'),
    },
    methods:{
        // 刷新表格内容
        update_col_page_data(){
            console.log("update_col_page_data")
            this.$store.dispatch("db/update_col_page_data",this.options);
        },
        // 下载表格内容
        download_page_data(){
            console.log("download_page_data")
            this.$store.dispatch("db/get_download_url",this.options);
        },
        // 删除筛选数据
        delete_filter_data(){

        }
    },
    watch:{
        options:{
            handler(){
                console.log("页面刷新",this.options)
                this.$store.dispatch("db/update_col_page_data",this.options);
            },
            deep:true,
        },
    }


}
</script>